import MyDate from './modules/myDate'
import api from './modules/api'

function getOrders(type) {
  api.usercenter
  .getOrders(type)
  .then((res) => {
    console.log(res)
  })
}
function getAfterOrders() {
  api.usercenter
  .getAfterOrders()
  .then((res) => {
    console.log(res)
  })
}
function checkin() {
  return api.usercenter.checkin()
}
function getUserInfo() {
  api.usercenter.getUserInfo().then((res) => {
    this.userinfo = res.data
  })
}

$(() => {
  const date = new MyDate()
  date.init()

  $('#checkin').click(function() {

    checkin().then(function(){
      $('#checkin span').text('已签到')
    })
   
  })
})



