export default class MyDate {
  constructor() {
    this.now = new Date()
    this.year = this.now.getFullYear()
    this.month = this.now.getMonth() + 1
  }

  init() {
    this.appendYear()
    this.appendMonth()
    this.appendDay()
    this.onYearChange()
    this.onMonthChange()
  }
  
  appendYear() {
    let html = ''
    const maxLen = 130
    for (let i = this.year; i >= this.year - maxLen; i--) {
      html += `<option value=${i}>${i}</option>`
    }
    $('#year').append(html)
  }

  appendMonth() {
    let html = ''
    const maxLen = 12
    for (let i = 1; i <= maxLen; i++) {
      html += `<option value=${i}>${i}</option>`
    }
    $('#month').append(html)
  }

  appendDay() {
    let html = ''
    const maxLen = this.getTotalDays(this.year, this.month)
    for (let i = 1; i <= maxLen; i++) {
      html += `<option value=${i}>${i}</option>`
    }
    $('#day').empty().append(html)
    $('#day').selectpicker('refresh')
  }

  onYearChange() {
    const _this = this
    $('#year').change(function() {
      _this.year = $(this).val()
      _this.appendDay()
    })
  }

  onMonthChange() {
    const _this = this
    $('#month').change(function() {
      _this.month = $(this).val()
      _this.appendDay()
    })
  }

  // 获取当前月份有几天
  getTotalDays(year, month) {
    return new Date(year, month, 0).getDate()
  }
}


