import axios from 'axios';
console.log(process.env.NODE_ENV);
var instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? 'http://localhost:8888/api' : 'http://mall.admin.zotye.com/api',
})
var whiteList = [
  '/UserLogin/user_login',
  '/UserReg/regcode',
  '/UserReg/reg',
  '/Article/pointDesc',
  '/Article/pointDesc',
  '/Article/auctionAgreement',
  '/Article/laws',
  '/Article/contact',
  '/Drive/showAppoint',
  '/Drive/show_province',
  '/Drive/check_children',
  '/Drive/addAppoint',
  '/goods/carSpec'
]

instance.interceptors.request.use(function(config){
  if(window.localStorage.getItem('token') && whiteList.indexOf(config.url)<0){
    if(!config.params) {
      config.params = {}
    }
    config.params['token'] = window.localStorage.getItem('token')
  }
  return config
})

instance.interceptors.response.use(function(res){
  return res.data
})

export default {
  order: {
    /**
     * @method
     * 创建订单
     * 
     * @param {String} action 购买类型 '固定值buy_now'
     * @param {String} exchange_integral 全积分商品 '固定值2'
     * @param {String} goods_id 商品id '27'
     * @param {String} goods_num 数量 '1'
     * @param {String} item_id 商品规格id '18128131762'
    */
    createOrder(data) {
      return instance.post('/Order/commit', {
        params: {
          ...data
        }
      })
    },
    // 提交订单
    commitOrder(params) {
      return instance.post('/Order/commit_order', {
        ...params
      })
    },
    // 支付
    payOrder(master_order_sn, type) {
      return instance.post('/Order/pay', {
        master_order_sn,
        type
      })
    },
    cancelOrder(order_id, master_order_sn) {
      return instance.post('/Order/delorder', {
        master_order_sn,
        order_id
      })
    },
    integralPay(master_order_sn) {
      return instance.post('/Order/pointPay', {
        master_order_sn
      })
    },
    deleteOrder(order_id) {
      return instance.post('/Order/deleteOrder', {
        order_id
      })
    }
  },
  usercenter: {
    // 登录
    login(mobile, password) {
      return instance.post('/UserLogin/user_login', {
        mobile,
        password
      })
    },
    // 验证码
    sendCode(mobile) {
      return instance.post('/UserReg/regcode', {
        mobile
      })
    },
    // 忘记密码获取验证码
    forgotCode(mobile) {
      return instance.post('/UserLogin/forgot_code', {
        mobile
      })
    },
    // 忘记密码修改密码
    forgotPwd({mobile, code, password}) {
      return instance.post('/UserLogin/forgot_password', {
        mobile, code, password
      })
    },
    // 注册
    register(mobile, code, password) {
      return instance.post('/UserReg/reg', {
        mobile,
        code,
        password
      })
    },
    // 订单列表
    getOrders(type, page=1) {
      return instance.post('/Order/userOrder', {}, {
        params: {
          page: 1,
          order_type: type
        }
      })
    },
    // 退款列表
    getAfterOrders() {
      return instance.post('/AfterSale/index')
    },
    // 签到
    checkin() {
      return instance.post('/users/sign_in') 
    },
    getUserInfo() {
      return instance.post('/users/userinfo') 
    },
    logout() {
      return instance.post('/UserLogin/logout')
    },
    // 收藏列表
    getCollection(page = 1) {
      return instance.post('/users/collection', {}, {
        params: {
          page
        }
      })
    },
    toggleFav(exchange_integral, goods_id) {
      return instance.get('/goods/collect', {
        params: {
          exchange_integral,
          goods_id
        }
      })
    },
    /**
     * @method
     * 查询签到日志
     * 
     * @param {String} today '2018/12/01'
    */
    querySignLog(today) {
      return instance.post('/users/sign_query')
    },
    /**
     * @method
     * 添加收货地址
     * 
     * @param {String} consignee 收件人 'xxx'
     * @param {String} mobile 电话 '1812813177'
     * @param {String} address 地址 'xxxxx'
     * @param {String} goods_num 数量 '1'
     * @param {String} item_id 商品规格id '18128131762'
     * @param {String} name 名字 'xxx'
     * @param {String} sex 性别 '1男2女'
     * @param {String} month 月份 '12'
     * @param {String} day 天 '25'
    */
    addAddress(data) {
      return instance.post('/UserAddress/add_address', {
        ...data
      })
    },
    getAddressList() {
      return instance.get('/UserAddress/index')
    },
    setUserinfo(data) {
      return instance.post('Users/update_info', data)
    }
  },
  article: {
    // 积分说明
    getPointDesc() {
      return instance.post('/Article/pointDesc')
    },
    // 拍卖服务协议
    getAuctionAgreement() {
      return instance.post('/Article/auctionAgreement')
    },
    // 法律声明
    getLaws() {
      return instance.post('/Article/laws')
    },
    // 联系我们
    contactUs() {
      return instance.post('/Article/contact')
    }
  },
  drive: {
    // 车型列表
    getDrivelist() {
      return instance.post('/Drive/showAppoint')
    },
    // 省列表
    getProvince() {
      return instance.post('/Drive/show_province')
    },
    // 城市列表
    getCity(id) {
      return instance.post('/Drive/check_children', {id})
    },
    /**
     * @method
     * 查询销售网点
     * 
     * @param {String} cate 车型类型id '4'
     * @param {String} province 省id '636'
     * @param {String} city 市id '1188'
    */
    querySales(cate, province, city) {
      return instance.post('/Drive/sales', {
        cate_id2: cate,
        province,
        city
      })
    },
    /**
     * @method
     * 查询经销商
     * 
     * @param {String} cate 车型类型id '4'
     * @param {String} province 省id '636'
     * @param {String} city 市id '1188'
    */
    queryDealers(cate, province, city) {
      return instance.post('/Drive/getDealers', {
        cate_id2: cate,
        province,
        city
      })
    },
    /**
     * @method
     * 预约
     * 
     * @param {String} category_id 车型类型id '4'
     * @param {String} province 省id '636'
     * @param {String} city 市id '1188'
     * @param {String} dealers_id 经销商id '97'
     * @param {String} mobile 手机号 '18128131762'
     * @param {String} name 名字 'xxx'
     * @param {String} sex 性别 '1男2女'
     * @param {String} month 月份 '12'
     * @param {String} day 天 '25'
    */
    addAppoint(data) {
      return instance.post('/Drive/addAppoint', {
        ...data
      })
    },
    getCarSpec(params) {
      return instance.get('/goods/carSpec', {
        params
      })
    }
  }
}


